	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/fotb-day-one/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/fotb1-1b9c2b1e0e52fa71e5a49ba48c91ee78-e8574.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 55.99999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"fotb1\"\n        title=\"\"\n        src=\"/static/fotb1-1b9c2b1e0e52fa71e5a49ba48c91ee78-e8574.jpg\"\n        srcset=\"/static/fotb1-1b9c2b1e0e52fa71e5a49ba48c91ee78-c94d9.jpg 158w,\n/static/fotb1-1b9c2b1e0e52fa71e5a49ba48c91ee78-9885b.jpg 315w,\n/static/fotb1-1b9c2b1e0e52fa71e5a49ba48c91ee78-e8574.jpg 500w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Wow.  That's another day gone - its funny really, you feel like you've been here for ages based on what you've seen, but then you step back and realise it's only Monday.</p>\n<p>John Davis' Keynote was a good introduction - teasing us with the features of Flash Player 10 - and setting us up for Wednesdays' session on \"The Ying and Yang of Flash\".  I missed the mornings session on <a href=\"http://www.flashonthebeach.com/sessions/index.php?pageid=2194\">“Mash-up widgets with Adobe AIR”</a>, but caught Brendan Halls' <a href=\"http://www.flashonthebeach.com/sessions/index.php?pageid=2112\">“Brilliant Ideas that I’ve blatantly stolen”</a> - which had some really interesting ideas - and saw Thermo in all its prerelease glory at Mark Anders' <a href=\"http://www.flashonthebeach.com/sessions/index.php?pageid=2174\">\"A Preview of Flex 4 and 'Thermo'\"</a></p>\n<p>Really interesting however was the session by Erik Natzke on <a href=\"http://www.flashonthebeach.com/sessions/index.php?pageid=2101\">\"Beyond the Knowledge: The Art of Play\"</a> where he showed how he's developed Flash into and art tool, and uses it to create works of art like the one above and those on <a href=\"http://www.flickr.com/photos/natzke/\">Flickr</a> and <a href=\"http://jot.eriknatzke.com/\">his blog</a>.  Inspiring stuff.</p>\n<p>As for the social side of things, Mark met '<a href=\"http://natejc.com/blog/\">the guy who wrote Tweener</a>' although I missed him.</p>\n<p>Beer and sessions are another inspired idea for networking, and we also met Tim and Kyle from <a href=\"http://www.teamcooper.co.uk/\">Team Cooper Ltd.</a>.</p>\n<p>On Tueday we've got sessions by Lee Brimelow and Grant Skinner so no doubt they'll be awesome.</p>","frontmatter":{"title":"FOTB: Day One","date":"September 30, 2008","tags":["flash","conferences","flash-on-the-beach","fotb"],"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/fotb-day-zero/","tags":["flash","conferences","flash-on-the-beach","ftob"],"title":"FOTB: Day Zero","date":"2008-09-28T19:57:07.000Z","description":null,"image":null}},"next":{"frontmatter":{"path":"/blog/fish-eye-menu-in-actionscript-20/","tags":["flash","actionscript"],"title":"Fish Eye Menu in ActionScript 2.0","date":"2008-10-12T13:01:05.000Z","description":null,"image":null}}}}

